<template>
    <LoadingComponent :props="loading" />
    <section v-if="benefits.length > 0" class="pt-8 pb-24 sm:py-12 border-t border-slate-100">
        <div class="container" style="position: relative;">
            <img class="w-full" src="/subscription.png" alt="">
            <div style="position: absolute; top: 189px; left: 67px;">
                <form @submit.prevent="saveSubscription" style="width: 672px;" class="mt-5 mb-6 block">
                    <div class="w-full h-10 bg-white">
                        <input style="margin-bottom: 19px;" type="email" v-model="subscriptionProps.post.email"
                            :placeholder="$t('label.your_email_address')" class="w-full h-full pl-3 pr-2">
                        <button type="submit" style="width: 672px; background-color: #000; color: white;"
                            class="text-xs font-semibold capitalize px-3 h-full text-white">
                            {{ $t('button.subscribe') }}
                        </button>
                    </div>
                </form>
            </div>
        </div>
    </section>
</template>

<script>
import statusEnum from "../../../enums/modules/statusEnum";
import LoadingComponent from "../components/LoadingComponent";
import axios from "axios";
import alertService from "../../../services/alertService";

export default {
    name: "SubscriptionBannerComponent",
    components: {
        LoadingComponent
    },
    data() {
        return {
            loading: {
                isActive: false,
            },
            subscriptionProps: {
                post: {
                    email: ""
                }
            }
        }
    },
    computed: {
        benefits: function () {
            return this.$store.getters["frontendBenefit/lists"];
        },
    },
    mounted() {
        this.loading.isActive = true;
        this.$store.dispatch("frontendBenefit/lists", {
            paginate: 0,
            order_column: "id",
            order_type: "asc",
            status: statusEnum.ACTIVE,
        }).then(res => {
            this.loading.isActive = false;
        }).catch((err) => {
            this.loading.isActive = false;
        });
    },
    methods: {
        saveSubscription: function () {
            const url = '/frontend/subscriber';
            this.loading.isActive = true;
            axios.post(url, this.subscriptionProps.post).then(res => {
                this.loading.isActive = false;
                this.subscriptionProps.post.email = "";
                alertService.success(this.$t("message.subscribe"));
            }).catch((err) => {
                this.loading.isActive = false;
            });
        }
    }
}
</script>
